@import url('https://fonts.googleapis.com/css2?family=Pacifico&display=swap');
*{
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Quicksand', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* background-color: #242a28; */
  background: rgb(139,62,191);
background: radial-gradient(circle, rgba(139,62,191,1) 31%, rgba(254,63,105,1) 100%);
  color: #e1eeeb;
}

.header {
  text-align: center;
  margin: 3rem auto;
}


.header img {
  width: 5rem;
  height: 5rem;
  object-fit: contain;
  background-color: transparent;
}

.header h1 {
  font-family: 'Pacifico', cursive;
  font-size: 1.8rem;
  letter-spacing: .1rem;
  font-weight: 400;
}

.form {
  padding: 1rem;
  max-width: 32rem;
  height: 16rem;
  margin: 2rem auto;
  border-radius: 2rem;
background: rgba(255, 255, 255, 0.146);
}

.input-group {
  display: flex;
  justify-content: space-evenly;
  gap: 1.5rem;
}

.form label {
  display: block;
  margin-bottom: 0.25rem;
  font-family: 'Pacifico', cursive;
  font-size: .5rem;
  font-weight: 400;
  letter-spacing: .025rem;
  text-transform: uppercase;
  
}

.form input {
  width: 100%;
  padding: 0.5rem;
  border: 1px solid rgba(254,63,105,1) ;
  border-radius: 0.25rem;
  background-color: transparent;
  color: #c2e9e0;
  font-size: 1rem;
}

.actions {
  display: flex;
  justify-content: center;
  gap: 1rem;
}

.button {
  padding: 0.5rem 1rem;
  border: none;
  border-radius: 0.25rem;
  background: rgb(159,95,203);
background: linear-gradient(90deg, rgba(159,95,203,1) 24%, rgba(255,70,110,1) 100%);
  color: #c2e9e0;
  font-family: 'Roboto Condensed', sans-serif;
  cursor: pointer;
  border-radius: 1rem;
}

.buttonAlt {
  font-family: 'Roboto Condensed', sans-serif;
  border: none;
  background: transparent;
  color: #c2e9e0;
  cursor: pointer;
}

button:hover {
  background: rgb(167,75,230);
  background: linear-gradient(17deg, rgba(167,75,230,1) 24%, rgba(255,70,110,1) 100%);

}
button:active{
  transform: translateY(10%);
}
.buttonAlt:hover {
  background: transparent;
 color:rgba(255,70,110,1);
}

.result {
  background-color: #ffffff24;
   max-width: 38rem;
   border-radius: 3rem;
  margin: 1rem auto;
  padding: 1rem;
  table-layout: fixed;
  border-spacing: 1rem;
  text-align: right;
}

.result thead {
  font-size: 0.7rem;
  color: #ffffff;

}

.result tbody {
  font-family: 'Pacifico', cursive;
  font-size: 0.85rem;
  color: #ffffff;
}

@media (max-width:600px) {
  .form{
    width: 20em;
  }
  .result{
    width: 21rem;
  }
  
}